<template>
  <div class="add">
    <div class="top box">
      <div class="row" @click="isOriginDateShow=true">
        <div class="left">
          个人班次:
        </div>
        <div class="right">
          <span>{{formData.originInfo.sheduling_date ? `${formatDateTime(formData.originInfo.sheduling_date)} ${formData.originInfo.arrange_order_id} ${formData.originInfo.time_interval}`: '请选择'}}</span>
          <img src="@/assets/imgs/qianjin.png" alt="">
        </div>
      </div>
      <div class="hr"></div>
      <div class="row" @click="isChangeUserShow=true">
        <div class="left">
          换班对象:
        </div>
        <div class="right" >
          <span>{{formData.changeUser.name ? `${formData.changeUser.name} (${formData.changeUser.jobnumber || '暂无'})`: '请选择'}}</span>
          <img src="@/assets/imgs/qianjin.png" alt="">
        </div>
      </div>
      <div class="hr"></div>
      <div class="row" @click="showChangeDate">
        <div class="left">
          换班班次:
        </div>
        <div class="right">
          <span>{{formData.changeInfo.sheduling_date ? `${formatDateTime(formData.changeInfo.sheduling_date)} ${formData.changeInfo.arrange_order_id}${formData.changeInfo.time_interval}`: '请选择'}}</span>
          <img src="@/assets/imgs/qianjin.png" alt="">
        </div>
      </div>
    </div>
    <div class="box middle">
      <div class="row">
        换班原因:
      </div>
      <div class="hr"></div>
      <div class="row textarea_box">
        <textarea name="" id="" rows="10" v-model="formData.commit" placeholder="请填写换班原因"></textarea>
      </div>
    </div>
    <common-btn class="add_btn" @click="commitChange">发起申请</common-btn>
    <my-dialog v-if="isOriginDateShow">
      <origin-info @selectOrigin="selectOrigin"></origin-info>
    </my-dialog>
    <my-dialog v-if="isChangeUserShow">
      <change-users @selectUser="selectUser" :changeInfo="formData.changeInfo"></change-users>
    </my-dialog>
    <my-dialog v-if="isChangeDateShow">
      <change-info @selectChange="selectChange" :changeUser="formData.changeUser"></change-info>
    </my-dialog>
  </div>
</template>

<script>
import ChangeUsers from './components/ChangeUsers'
import OriginInfo from './components/OriginInfo'
import ChangeInfo from './components/ChangeInfo'
export default {
  name: 'ApplicationAdd',
  components: {
    ChangeUsers,
    OriginInfo,
    ChangeInfo
  },
  data () {
    return {
      isOriginDateShow: false,
      isChangeUserShow: false,
      isChangeDateShow: false,
      formData: {
        originInfo: {},
        changeUser: {},
        changeInfo: {},
        commit: ''
      }
    }
  },
  methods: {
    selectUser (user) {
      this.formData.changeUser = user
      this.isChangeUserShow = false
    },
    selectOrigin (origin) {
      this.formData.originInfo = origin
      this.isOriginDateShow = false
    },
    selectChange (change) {
      this.formData.changeInfo = change
      this.isChangeDateShow = false
    },
    showChangeDate () {
      if (this.formData.changeUser && this.formData.changeUser.id) {
        this.isChangeDateShow = true
      } else {
        alert('请选择换班对象')
      }
    },
    // 格式化时间
    formatDateTime (v) {
      if (v) {
        return this.$moment(v).format('MM-DD')
      }
    },
    commitChange () {
      if (this.formData.originInfo.id && this.formData.changeUser.id && this.formData.changeInfo.id) {
        console.log(this.formData)
        this.$rpc.call('funenc_xa_station.change_shifts', 'save_change_shifts', [], {
          personal_change_shifts_1: this.formData.originInfo.id,
          change_shifts_user_id: this.formData.changeUser.id,
          change_shifts_group_1: this.formData.changeInfo.id,
          reason: this.formData.commit
        }).then(res => {
          console.log('res', res)
          if (res) {
            this.$router.push({
              path: '/changeapplication'
            })
          }
        }).catch(e => {
          console.log(e)
          alert('换班失败')
        })
      } else {
        console.log('信息不全')
      }
    }
  }
}
</script>

