<template>
  <div class="change_user">
    <div v-for="item in userData" :key="item.id" class="content">
      <div class="row" @click="selectItem(item)">
        <img :src="item.avatar" alt="">
        <div class="name">{{item.name|| '暂无'}}</div>
        <div class="job_nub">{{item.jobnumber|| '暂无'}}</div>
        <div class="position">{{item.position || '暂无'}}</div>
      </div>
      <div class="hr"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ChangeUsers',
  props: ['changeInfo'],
  data () {
    return {
      userData: [
        {
          id: 1,
          name: '张三',
          jobnumber: 'Q123412',
          position: '值班站长',
          avatar: 'https://wx3.sinaimg.cn/crop.0.4.1280.711/b10d83d8gy1fu76dzn2mtj20zk0k0gre.jpg'
        }
      ]
    }
  },
  methods: {
    selectItem (user) {
      this.$emit('selectUser', user)
    },
    getShedulingObject () {
      this.$rpc.call('funenc_xa_station.sheduling_record', 'get_sheduling_object', [], {}).then(res => {
        this.userData = res
      }).catch(e => {
        console.log(e)
        alert('获取换班对象失败')
      })
    }
  },
  mounted () {
    console.log(this.changeInfo.id)
    this.getShedulingObject()
  }
}
</script>

